@mixin font($font-name, $file-name, $weight: normal, $style: normal) {
  @font-face {
    font-family: quote($font-name);
    src: url($file-name + '.eot');
    src: url($file-name + '.eot?#iefix')  format('embedded-opentype'),
    url($file-name + '.woff') format('woff'),
    url($file-name + '.ttf')  format('truetype') {}
    font-weight: $weight;
    font-style: $style; } }

@include font("ProximaNova", "../fonts/ProximaNova-Extrabld", 600);
@include font("ProximaNova", "../fonts/ProximaNova-Semibold");
@include font("Roboto", "../fonts/Roboto-Regular");
@include font("Roboto", "../fonts/Roboto-Bold", 600);
@include font("Roboto", "../fonts/Roboto-Medium", 500);
@include font("Roboto", "../fonts/Roboto-Italic", normal, italic);




