@import "fonts";
@import "slick";
@import "slick-theme";
$dark: #283949;
$yellow: #FFA300;

@function adpt($before, $after, $before_width, $after_width) {
    $m : ($before - $after) / ($before_width - $after_width);
    $b : ($after - $m * $after_width);
    $result_vw : $m * 100vw;
    $result_px : $b+px;
    $result : $result_vw + $result_px;
    @return calc(#{$result_vw} + #{$result_px}); }


h1 {
    font-family: ProximaNova;
    font-size: 64px;
    font-weight: normal;
    color: $dark; }
h2 {
    font-family: ProximaNova;
    font-size: 48px;
    font-weight: normal;
    color: $dark; }
p {
    font-family: Roboto;
    font-size: 24px; }
a {
    color: inherit;
    text-decoration: none; }
li {
    list-style-type: none; }

* {
    margin: 0;
    padding: 0; }

@keyframes fadeInRight {
    0% {
        opacity: 1;
        transform: translate(50%, 0); }
    100% {
        opacity: 1;
        transform: translate(0, 0); } }


header {
    background-image: url(../img/bg.webp);
    background-image: url(../img/bg.png);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 0 20%;
    position: relative;
    .head {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 10px 50px 10px 0;
        margin: 0 auto;
        z-index: 1;
        .mail {
            margin-right: 10px; }
        img {
            width: 350px; }
        .contacts {
            div {
                display: flex;
                align-items: center;
                &:nth-child(2) {
                    margin-top: 10px; }
                p,
                a {
                    font-family: ProximaNova;
                    font-size: 16px; }
                i {
                    font-size: 20px;
                    margin: 0 10px 0 5px; } } } } }
.ham {
    cursor: pointer;
    -webkit-tap-highlight-color: transparent;
    transition: transform 400ms;
    user-select: none;
    position: fixed;
    right: 10px;
    z-index: 5;
    display: none;
    .top {
        stroke-dasharray: 40 160; }
    .middle {
        stroke-dasharray: 40 142;
        transform-origin: 50%;
        transition: transform 400ms; }
    .bottom {
        stroke-dasharray: 40 85;
        transform-origin: 50%;
        transition: transform 400ms, stroke-dashoffset 400ms; }
    &.active {
        .top {
            stroke-dashoffset: -64px; }
        .middle {
            transform: rotate(90deg); }
        .bottom {
            stroke-dashoffset: -64px; } } }
.hamRotate {
    &.active {
        transform: rotate(45deg); } }
.line {
    fill: none;
    transition: stroke-dasharray 400ms, stroke-dashoffset 400ms;
    stroke: $dark;
    stroke-width: 5.5;
    stroke-linecap: round; }
.overlay {
    position: fixed;
    background: #ffffff;
    top: 0;
    right: 0;
    width: 70%;
    height: 100%;
    opacity: .8;
    transform-origin: right;
    transform: translate(100%, 0);
    visibility: hidden;
    transition: all 0.3s ease-in-out;
    overflow: hidden;
    z-index: 4;
    .overlayBorder {
        width: 100%;
        height: 3px;
        background-color: #000; }
    li {
        display: flex;
        justify-content: center;
        align-items: flex-end;
        flex-direction: column;
        font-family: ProximaNova;
        font-size: adpt(26, 12, 767, 320);
        color: #000;
        opacity: 0;
        padding: 10px 0;
        padding-right: adpt(30, 15, 767, 320);
        .number {
            display: flex;
            align-items: center;
            margin-top: 10px; }
        i {
            font-size: 30px;
            margin-left: 4px; } }


    &.open {
        opacity: 0.99;
        visibility: visible;
        transform: scale(1);
        li {
            animation: fadeInRight 0.1s cubic-bezier(0.42, 0.83, 0.7, 1.5) forwards;
            animation-delay: 0.4s;
            transform: rotate(-7deg);
            transform-origin: right bottom;
            &:nth-of-type(1) {
                 margin-top: 0; }
            &:nth-of-type(2) {
                animation-delay: 0.5s; }
            &:nth-of-type(3) {
                animation-delay: 0.6s; }
            &:nth-of-type(4) {
                animation-delay: 0.7s; }
            &:nth-of-type(5) {
                animation-delay: 0.8s; } } }
    ul {
        text-align: right;
        padding: 60px 0; } }
.locked {
    overflow: hidden; }
.darken {
    background-color: rgba(0, 0, 0, 0.8);
    width: 100%;
    height: 500%;
    position: absolute;
    z-index: 3; }


nav {
    background-color: $dark;
    position: absolute;
    top: 81.6px;
    width: 100%;
    z-index: 10;
    ul {
        max-width: 1440px;
        margin: 0 auto;
        display: flex;
        justify-content: center;
        padding: 10px 0;
