            color: #ffffff;
            text-transform: uppercase;
            font-family: ProximaNova;
            font-weight: 600;
            font-size: 20px;
            margin-right: 80px;
            transition: color .3s ease;
            line-height: 29px;
            &:nth-last-child(1) {
                margin-right: 0; }
            &:hover {
                color: $yellow;
                transition: color .3s ease; }
            &.active {
