.slickPrev,
.slickNext {
    position: absolute;
    display: block;
    top: 50%;
    transform: translate(0, -50%);
    z-index: 1;
    font-size: adpt(150, 50, 1440, 320);
    color: #ffffff;
    opacity: .6;
    cursor: pointer;
    transition: opacity .2s ease;
    &:hover {
        opacity: 1;
        transition: opacity .2s ease; } }
.slickNext {
    right: adpt(40, 0, 1440, 320); }
.slickPrev {
    left: adpt(20, 0, 1440, 320); }
.carousel {
