table {
    font-family: Roboto;
    margin: 0 auto;
    margin-top: adpt(30, 10, 1440, 320);
    width: 100%;
    max-width: 1250px;
    border-spacing: adpt(20, 6, 1440, 320);
    tr {
        width: 100%; }
    th,
    td {
        border: 2px solid $dark;
        padding: adpt(20, 10, 1440, 320) adpt(40, 10, 1440, 320);
        text-align: center;
        font-size: adpt(17, 10, 1400, 320);
        &:nth-child(1) {
            width: 40%; }
        &:nth-child(2) {
            width: 20%; }
        &:nth-child(3) {
            width: 20%; } }
    td {
        a {
            display: inline-block; }
        &:nth-child(1) {
            cursor: pointer;
            &:hover {
                box-shadow: inset 10px 10px 4px rgba(0, 0, 0, 0.2), inset -10px -10px 4px rgba(0, 0, 0, 0.2);
                tramsition: all .2s ease; } } }
    th {
        background-color: $dark;
        color: $yellow; } }



.price-list {
    padding: 50px 50px;
    .price-table {
        padding: 30px 90px 50px;
        display: grid;
        grid-template-columns: 2.8fr 1.62fr 1.62fr;
        text-align: center;
        grid-column-gap: 90px;
        grid-row-gap: 20px;
        .table__title {
            background-color: $dark;
            border: none;
            color: $yellow; }
        p {
            border: 2px solid black;
            padding: 15px 10px;
            font-weight: 500;
            font-size: adpt(24, 13, 1440, 1025);
            &.dual {
                padding: adpt(2, 7, 1440, 1025) 20px; } }
        a {
            transition: all .2s ease;
            &:hover {
                box-shadow: inset 10px 10px 4px rgba(0, 0, 0, 0.2), inset -10px -10px 4px rgba(0, 0, 0, 0.2);
                tramsition: all .2s ease; } } } }
.services-disc {
    padding: 30px 50px 100px;
    overflow-x: hidden;
    h2 {
        text-align: center; }
    .underline {
        background-color: $yellow;
        height: 7px; }
    img {
        width: adpt(460, 360, 1440, 1025); }
    .disc-item {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-top: 50px;
        &:nth-of-type(2) {
            margin-top: 115px; }
        p {
            margin-left: adpt(100, 60, 1440, 1025);
            color: #000;
            line-height: 136%;
            font-size: adpt(24, 18, 1440, 1025); }
        &.right {
            picture {
                order: 2; }
            p {
                order: 1;
                margin-left: 0;
                margin-right: adpt(100, 60, 1440, 1025);
                text-align: right; } } } }
.portfolio {
    padding: 30px adpt(50, 30, 1440, 1025);
    overflow-x: hidden;
    .container {
        display: grid;
        grid-template-columns: 3.15fr 9fr;
        margin-top: 60px;
        grid-column-gap: adpt(112, 50, 1440, 1025); } }
.filter {
    font-family: ProximaNova;
    font-weight: normal;
    border-right: 6px solid $yellow;
    padding: 0 50px 0 16px;
    display: block;
    .active {
        border: 4px solid $yellow; }
    li {
        font-family: ProximaNova;
        font-size: adpt(24, 20, 1440, 1025);
        color: $dark;
        position: relative;
        overflow: hidden;
        display: inline-block;
        &.active {
            border: 4px solid $yellow;
            margin: 5px 0;
            &::before,
            &::after {
                background: transparent; }
            a {
                &::before,
                &::after {
                    background: transparent; } } }
        &::before,
        &::after {
            content: '';
            position: absolute;
            transition: .5s ease;
            right: 0;
            bottom: 0;
            background: $yellow; }
        &::before {
            width: 100%;
            height: 4px;
            transform: translateX(-100%); }
        &::after {
            width: 4px;
            height: 100%;
            transform: translateY(100%); }
        &:hover {
            &::before,
            &::after {
                transform: translate(0, 0); } }
        &:hover {
            a {
                &::before,
                &::after {
                    transform: translate(0, 0); } } }
        a {
            display: block;
            padding: 10px;
            &::before,
            &::after {
                content: '';
                left: 0;
                position: absolute;
                transition: .5s ease;
                top: 0;
                background: $yellow; }
            &::before {
                width: 100%;
                height: 4px;
                transform: translateX(100%); }
            &::after {
                width: 4px;
                height: 100%;
                transform: translateY(-100%); } } } }

.portfolio-items {
    display: grid;
    padding-top: 20px;
    grid-template-columns: repeat(3, adpt(250, 200, 1440, 1025));
    grid-column-gap: adpt(40, 20, 1440, 1025);
    grid-row-gap: adpt(40, 20, 1440, 1025);

    .p-item {
        width: adpt(250, 200, 1440, 1025);
        height: adpt(250, 200, 1440, 1025);
        background-color: #c4c4c4;
        margin-bottom: 20px;
        cursor: pointer;
        background-size: cover;
        transition: box-shadow .3s ease;
        &:hover {
            box-shadow: 0px -1px 12px 5px $dark;
            transition: box-shadow .3s ease; }
        img {
            width: 100%;
            height: 100%; } } }

#toTop {
    padding: 16px 20px;
    background-color: black;
    text-align: center;
    border-radius: 4px;
    position: fixed;
    bottom: adpt(30, 10, 1440, 320);
    right: adpt(30, 10, 1440, 320);
    transition: all .3s ease;
    cursor: pointer;
    opacity: 0;
    display: inline-block;
    &.show {
        opacity: .7; }
    i {
        color: $yellow; }
    &:hover {
        opacity: 1; } }
.overlay-modal {
	width: 100%;
	min-height: 100vh;
	display: flex;
	justify-content: center;
	align-items: center;
	padding: 40px;
	position: fixed;
	top: 0;
	left: 0;
	background: rgba(black, 0.75);
	opacity: 0;
	pointer-events: none;
	transition: 0.35s ease-in-out;
	max-height: 100vh;
	overflow-y: auto;
	&.open {
		opacity: 1;
		pointer-events: inherit; }
	.modal {
		background: white;
		padding: 40px 80px;
		box-shadow: 0px 1px 10px rgba(white, 0.35);
		opacity: 0;
		pointer-events: none;
		transition: 0.35s ease-in-out;
		max-height: 100vh;
		overflow-y: auto;
		&.open {
			opacity: 1;
			pointer-events: inherit; } } }
.explanation {
    text-align: right; }
.modal__carousel {
    width: 100%;
    max-width: 800px;
    margin: 0 auto;
    margin-top: 20px;
    .slickPrev,
    .slickNext {
        font-size: 64px;
        color: #000; }
    .slickPrev {
        left: -70px; }
    .slickNext {
        right: -70px; } }
.overlay-modal {
    box-sizing: border-box;
    overflow-y: hidden;
    &.open {
        z-index: 11; }
    .modal {
        box-sizing: border-box;
        position: relative;
        width: 90%;
        max-width: 1400px;
        -ms-overflow-style: none;
        overflow: -moz-scrollbars-none;
        &::-webkit-scrollbar {
            width: 0; }
        p {
            font-size: adpt(20, 9, 1440, 320);
            line-height: adpt(40, 20, 1440, 320);
            &:nth-of-type(1) {
                margin-top: adpt(40, 20, 1400, 320); } }
        h2 {
            font-size: adpt(50, 15, 1440, 320);
            text-align: center; }
        .img {
            width: 650px;
            height: adpt(500, 300, 1440, 768);
            margin-top: adpt(40, 20, 1400, 320);
            &.red {
                background-color: red; }
            &.blue {
                background-color: blue; }
            img {
                width: 100%;
