            &::after {
                transform: rotate(-45deg); } }
        .nav-carousel {
            display: flex;
            width: 100%;
            justify-content: center;
            align-items: center;
            margin-top: 20px;
            .slickPrev,
            .slickNext {
                font-size: 44px;
                color: #000; }
            .slickPrev {
                left: -50px; }
            .slickNext {
                right: -40px; }
            .img {
                width: 120px;
                height: 150px;
                margin-right: 10px;
                cursor: pointer;
                &:nth-last-child(1) {
                    margin-right: 0; } } } } }










